let state = {
    isLogin: true
}

let mutations = {
    doLogin(state, payload) {
        if (payload.username == 'zl' && payload.password == 'zl') {
            window.localStorage.setItem('login', 'true')
            window.history.back()
            state.isLogin = !state.isLogin;
        }
    }
}

export default {
    namespaced: true,
    state,
    mutations
}


