import { getColorList } from '../../api/index'

let state = {
    colorList: {},
    colorId: '',
    lists: []
}

let mutations = {
    changeColor: (state, payload) => {
        console.log('sta...', state)
        let arr = []
        for (var i in payload.list) {
            arr.push(i)
        }
        arr.sort((a, b) => {
            return a - b
        })
        state.colorList = payload.list;
        console.log('////', state.colorList)
    },
    changYear: (state, payload) => {
        // alert(1)
        // console.log('st...', state)
        state.colorId = payload
            // console.log(state.colorList)
        for (var i in state.colorList) {
            if (payload == i) {
                state.lists = state.colorList[i]
            }
        }

        console.log(state.lists)
    }
}

let actions = {
    getColorLists: ({ commit }, payload) => {
        console.log('pay..1.', payload)
        getColorList(payload.id).then(body => {
            console.log('bo...', body.data)
            commit('changeColor', {
                id: payload,
                list: body.data
            })
        })
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}


