import { getDealerList } from '../../api/index'

let state = {
    getList: '',
    car_id: '129491',
    CarId: '201',
    cityId: 1,
    isShowCity: false,
    checkCity: '',
    isShowDetail: false,
    currentCity: ''
}



let mutations = {
    getDealerList(state, payload) {
        console.log('stat...', state, payload)
        state.getList = payload
    },
    showCity(state, payload) {
        alert(1)
        state.isShowCity = true
    },
    detail: (state, payload) => {
        payload.map((item, index) => {
            return Number(item.vendorPrice).toFixed(2);
        })
        state.getList = payload;
        state.isShowCity = false;
        state.isShowDetail = false;
    },
}

let actions = {
    getDealerLists({ commit }, payload) {
        getDealerList(state.car_id, state.cityId)
            .then(body => {
                commit('getDealerList', body.data.list)
                console.log('pay..', body.data.list)
            })
    },
    check: ({ commit }, payload) => {
        const { name, id } = payload
        state.checkCity = name;
        getDealerList(state.car_id, id).then(res => {
            commit('detail', res.data.list)
        })
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,

}


