import { getImgList } from '../../api/index'

let state = {
    imgList: [],
    currId: []
}

let mutations = {
    changeImg: (state, payload) => {
        console.log('states...', state)
        state.imgList = payload.list
        state.imgList.map((item) => {
            item.List.map((val) => {
                let imgs = val.Url.split('{')[0]
                val.Url = imgs + '3.jpg'
            })
        })
        state.currId = payload
        console.log('payload...', payload)
    }

}

let actions = {
    getImgLists: ({ commit }, payload) => {
        console.log('payload...', payload)
        getImgList(payload).then(body => {
            console.log('body...', body.data)
            commit('changeImg', {
                id: payload,
                list: body.data
            })
        })
    }
}


export default {
    namespaced: true,
    state,
    mutations,
    actions
}


