import { getBrandList, ToppedList, DatailList } from '../../api/index'

let state = {
    //首字母
    Acronyms: [],
    //品牌列表
    MotorcycleType: {},
    isShow: false,
    //品牌id
    brandId: '',
    //车系列表
    ToppedList: [],
    isShowList: false,

}

let mutations = {
    initState: (state, payload) => {
        console.log('..payload', payload)
            //处理数据
        let Acronyms = [];
        let MotorcycleType = {}
        payload.forEach((item, index) => {
            //console.log(item)
            let Spell = item.Spelling[0]
                //console.log(Spell)
            if (Acronyms.indexOf(Spell) != -1) {
                MotorcycleType[Spell].push(item)
            } else {
                Acronyms.push(Spell)
                MotorcycleType[Spell] = [item]
            }
            state.Acronyms = Acronyms
            state.MotorcycleType = MotorcycleType
                //console.log(Acronyms, MotorcycleType)

        })
    },
    touchstart: (state, payload) => {
        state.isShow = true;

    },
    touchend: (state, payload) => {
        setTimeout(function() {
            state.isShow = false
        }, 1000)
    },
    changeToppedList: (state, payload) => {
        state.brandId = payload.id;
        state.ToppedList = payload.list;
        state.isShowList = true
    },
    hideMakeList: (state, payload) => {
        state.isShowList = false;
    },


}

let actions = {
    initState: (context, payload) => {
        // console.log('hello')
        getBrandList().then(body => {
            // console.log(body.data);
            context.commit('initState', body.data)
        })
    },
    ToppedList: ({ commit, state }, payload) => {
        console.log('id..', payload)
        ToppedList(payload).then(body => {
            console.log('body...', body.data)
            commit('changeToppedList', {
                id: payload,
                list: body.data
            })
        })
    },

}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}


