import { DatailList } from '../../api/index'


let state = {
    //详情页
    detaiList: {},
    currentYear: '全部'
}

let mutations = {
    changeYear(state, payload) {
        state.currentYear = payload;
    },
    changeDetail: (state, payload) => {
        console.log('state.....', state, payload.list)
        state.detaiList = payload.list
            //console.log('state.detaiList....', state.detaiList.list)
        if (!state.detaiList) {
            return []
        }
        //车型数据排序
        state.detaiList.list.sort((a, b) => {
            //按照年份排序
            if (b.market_attribute.year != a.market_attribute.year) {
                return b.market_attribute.year - a.market_attribute.year
            } else {
                //按照排量排序
                if (b.exhaust_str > a.exhaust_str) {
                    return -1
                } else if (b.exhaust_str < a.exhaust_str) {
                    return 1
                } else {
                    //按照功率排序
                    if (b.max_power_str > a.max_power_str) {
                        return -1
                    } else if (b.max_power_str < a.max_power_str) {
                        return 1
                    } else {
                        //按照指导价格排序
                        if (b.market_attribute.dealer_price_min > a.market_attribute.dealer_price_min) {
                            return -1
                        } else if (b.market_attribute.dealer_price_min < a.market_attribute.dealer_price_min) {
                            return 1
                        }
                    }
                }
            }
        })

    }
}

let getters = {
    years(state) {
        if (!state.detaiList.list) {
            return []
        }
        let arr = state.detaiList.list.map((item) => {
            console.log('item...', item.market_attribute.year)
            return item.market_attribute.year
        })
        let set = new Set(arr)
        return ['全部', ...set]
    },
    lists(state) {
        if (!state.detaiList.list) {
            return []
        }
        //把排量和吸气类型相同的数据合并
        function merge(arr) {
            let types = [];
            let obj = {}
                //console.log('arr..', arr)
            arr.forEach((item) => {
                let type = item.exhaust_str + '/' + item.max_power_str + '' + item.inhale_type
                if (types.indexOf(type) == -1) {
                    types.push(type)
                    obj[type] = [item]
                } else {
                    obj[type].push(item)
                }
            })
            return obj
        }
        if (state.currentYear == '全部') {
            return merge(state.detaiList.list)
        } else {
            return merge(state.detaiList.list.filter((item => {
                return item.market_attribute.year == state.currentYear;
            })))
        }
    }

}

let actions = {
    DatailLists: ({ commit }, payload) => {
        console.log('ids', payload)
        DatailList(payload).then(body => {
            console.log('det....', body.data)
            commit('changeDetail', {
                id: payload,
                list: body.data
            })
        })
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    getters,
    actions
}


