import Vue from 'vue'
import Router from 'vue-router'
// import Index from '../components/Index.vue'

//引入组件
const Index = () =>
    import ('../components/Index')
const Color = () =>
    import ('../components/Color')
const Detail = () =>
    import ('../components/Detail')
const Follor = () =>
    import ('../components/Follor')
const Img = () =>
    import ('../components/Img')
const Type = () =>
    import ('../components/Type')
const login = () =>
    import ('../components/Login')

Vue.use(Router)

const router = new Router({
    mode: 'hash',
    routes: [{
            path: '/index',
            name: 'Index',
            component: Index
        },
        {
            path: '/color',
            component: Color
        },
        {
            path: '/follor',
            component: Follor
        },
        {
            path: '/detail',
            component: Detail

        },
        {
            path: '/img',
            component: Img
        },
        {
            path: '/type',
            component: Type
        },
        {
            path: '/login',
            component: login
        },
        {
            path: '*',
            redirect: '/index'
        }
    ]
})
router.beforeEach(function(to, from, next) {
    let login = window.localStorage.getItem('login')
    if (!login && to.path != '/login') {
        next('/login')
    }

    next();

})

export default router;


